import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, Loading, LoadingController, Alert  } from 'ionic-angular';

import { InAppBrowser , InAppBrowserOptions } from '@ionic-native/in-app-browser';

/**
 * Generated class for the AboutPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

declare var window:any;

@IonicPage()
@Component({
  selector: 'page-about',
  templateUrl: 'about.html',
})
export class AboutPage {

  alert: AlertController;
  public loading: Loading;

  options : InAppBrowserOptions = {
    location : 'yes',//Or 'no' 
    hidden : 'no', //Or  'yes'
    clearcache : 'yes',
    clearsessioncache : 'yes',
    zoom : 'yes',//Android only ,shows browser zoom controls 
    hardwareback : 'yes',
    mediaPlaybackRequiresUserAction : 'no',
    shouldPauseOnSuspend : 'no', //Android only 
    closebuttoncaption : 'Close', //iOS only
    disallowoverscroll : 'no', //iOS only 
    toolbar : 'yes', //iOS only 
    enableViewportScale : 'no', //iOS only 
    allowInlineMediaPlayback : 'no',//iOS only 
    presentationstyle : 'pagesheet',//iOS only 
    fullscreen : 'yes',//Windows only    
};

  constructor(public navCtrl: NavController, public navParams: NavParams, public loadingCtrl: LoadingController,
    public alertCtrl: AlertController, private theInAppBrowser: InAppBrowser) {
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad AboutPage');
  }

  public openWithSystemBrowser(url : string){
    let target = "_system";
    this.theInAppBrowser.create(url,target,this.options);
}
public openWithInAppBrowser(url : string){
    let target = "_blank";
    this.theInAppBrowser.create(url,target,this.options);
}
public openWithCordovaBrowser(url : string){
    let target = "_self";
    this.theInAppBrowser.create(url,target,this.options);
}  


 ShareApp(){
  var options = {
    message: 'Library Book Search', // not supported on some apps (Facebook, Instagram)
    subject: 'Try this Excellent app', // fi. for email
    files: ['', ''], // an array of filenames either locally or remotely
    url: 'http://myapps.byethost12.com/librarysearch/',
    chooserTitle: 'Choose an app' // Android only, you can override the default share sheet title
  }
  
  var onSuccess = function(result) {
    console.log("Share completed? " + result.completed); // On Android apps mostly return false even while it's true
    console.log("Shared to app: " + result.app); // On Android result.app is currently empty. On iOS it's empty when sharing is cancelled (result.completed=false)
  }
  
  var onError = function(msg) {
    console.log("Sharing failed with message: " + msg);
  }
  
  window.plugins.socialsharing.shareWithOptions(options, onSuccess, onError);
 }

 about(){

    const alert: Alert = this.alertCtrl.create({
      title: 'Library Book Search App \n\n',
      message: "Library Book Search is the reduce the wastage of time while searching an particular in book from library"+" \n\n developed by \n\n PRASAD BOBBY ðŸ˜ŽÂ ",
      buttons: ['OK']
    });
    alert.present();
 }

 feedback(){

  window.plugins.socialsharing.shareViaEmail(
    '#Issue', 
    'Library Search @Feedback',
    ['pssoftwares22@gmail.com'],  
    null
  );

 }

  version(){

window.plugins.toast.showWithOptions(
  {
    message: "Latest Version ðŸ”¥",
    duration: "short", // which is 2000 ms. "long" is 4000. Or specify the nr of ms yourself.
    position: "bottom",
    addPixelsY: -40  // added a negative value to move it up a bit (default 0)
  },


);
  }
}



